
@charset "UTF-8";
html, body {
  width: 100%;
  height: 100%;
}
body {
  background: url("./assets/images/bg.jpg") no-repeat;
  background-size: cover;
  font-size: 20px;
}
#app {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
}
.justify-around {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-align-items: center;
}
.justify-center {
  justifiy-content: space-between;
  -webkit-justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-align-items: center;
}
header {
  padding: .3rem .5rem 0;
}
.logo {
  width: 4.25rem;
  height: auto;
}
.btn_g {
  background: #5b5235;
  font-size: .6rem;
  color: #fff;
  border: none;
  outline: none;
  position: relative;
}
.head {
  padding: .3rem .6rem;
  margin: 0 .25rem;
}
.foot {
  padding: .3rem .5rem;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin: 0 .2rem;
}
.btn_g::before {
  content: "";
  width: .15rem;
  height: 100%;
  background: url("./assets/images/btn_le.png") no-repeat;
  background-size: cover;
  position: absolute;
  left: -.15rem;
  top: 0;
}
.btn_g::after {
  content: "";
  width: .15rem;
  height: 100%;
  background: url("./assets/images/btn_ri.png") no-repeat;
  background-size: cover;
  position: absolute;
  right: -.15rem;
  top: 0;
}
.main_box {
  width: 93%;
  height: 84%;
  background: url("./assets/images/bor.png") no-repeat;
  background-size: 100% 100%;
  margin: .5rem auto 0;
}
.footer {
  width: 93%;
  margin: .5rem auto 0;
}
.mask {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 10;
}
.pres_gift {
  width: 84%;
  height: 50%;
  background: url("./assets/images/tan_bg.png") no-repeat;
  background-size: 100% 100%;
  position: fixed;
  left: 8%;
  top: 20%;
  z-index: 13;
}
.pres_gift .sorry {
    width: 44%;
    height: auto;
    display: block;
    margin: 3rem auto 0;
}
.pres_gift p {
    font-size: .75rem;
    margin-top: .75rem;
    text-align: center;
    color: #fff;
}
.pres_gift .foot2 {
    padding: .3rem 1rem;
    display: block;
    margin: 3rem auto 0;
}
.pres_gift .foot3 {
    padding: .3rem 1rem;
    display: block;
    margin: 2rem auto 0;
}
.gift_box {
  width: 80%;
  margin-left: 10%;
  margin-top: 1rem;
  padding-bottom: .5rem;
  border-bottom: 1px solid #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  height: 2.75rem;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}
.gift_box span {
    font-size: .7rem;
    color: #fff;
}
.gift_box .quan {
    width: 6.5rem;
    height: 100%;
    background: url("./assets/images/imgs_bg.png") no-repeat;
    background-size: 100% 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    display: -webkit-flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 0 .3rem;
    position: relative;
}
.quan_cash {
  font-size: 2.4rem;
  font-weight: bold;
  color: #872f2a;
}
.quan_yu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-justify-content: flex-start;
}
.quan_yu span:nth-child(1) {
    font-size: .6rem;
    color: #fff;
    width: 1rem;
    height: 1rem;
    display: block;
    text-align: center;
    line-height: 1rem;
    background: #000;
    border-radius: 50%;
    font-family: "宋体";
}
.quan_yu span:nth-child(2) {
    font-size: .8rem;
    color: #872f2a;
    font-family: "宋体";
}
.msg {
  padding: .4rem .6rem;
  font-size: .7rem;
  color: #fff;
  background: rgba(0, 0, 0, 0.6);
  border-radius: .1rem;
  position: absolute;
  top: 10%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 15;
}

